<template>
  <div class="arena-controls">
    <div class="movement-controls">
      <!-- Virtual joystick container -->
      <div 
        ref="joystickContainer" 
        class="joystick-container"
        @touchstart="handleStart"
        @touchmove="handleMove"
        @touchend="handleEnd"
        @mousedown="handleStart"
        @mousemove="handleMove"
        @mouseup="handleEnd"
        @mouseleave="handleEnd"
      >
        <div 
          ref="joystick"
          class="joystick"
          :style="joystickStyle"
        ></div>
      </div>
    </div>
    
    <div class="action-controls">
      <button 
        class="action-button jump" 
        @touchstart.prevent="handleJump"
        @mousedown.prevent="handleJump"
      >
        JUMP
      </button>
      <button 
        class="action-button attack"
        @touchstart.prevent="handleAttack"
        @mousedown.prevent="handleAttack"
      >
        ATTACK
      </button>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'

const joystickContainer = ref(null)
const joystick = ref(null)
const joystickPos = ref({ x: 0, y: 0 })
const isJoystickActive = ref(false)
const maxJoystickDistance = 50 // Maximum distance joystick can move from center

// Compute joystick position style
const joystickStyle = computed(() => ({
  transform: `translate(${joystickPos.value.x}px, ${joystickPos.value.y}px)`
}))

const getEventPosition = (event) => {
  const touch = event.touches ? event.touches[0] : event
  const container = joystickContainer.value.getBoundingClientRect()
  return {
    x: touch.clientX - container.left,
    y: touch.clientY - container.top
  }
}

const handleStart = (event) => {
  event.preventDefault()
  isJoystickActive.value = true
  updateJoystickPosition(event)
}

const handleMove = (event) => {
  if (isJoystickActive.value) {
    event.preventDefault()
    updateJoystickPosition(event)
  }
}

const handleEnd = (event) => {
  event.preventDefault()
  isJoystickActive.value = false
  joystickPos.value = { x: 0, y: 0 }
  emit('move', { x: 0, y: 0 })
}

const updateJoystickPosition = (event) => {
  const pos = getEventPosition(event)
  const container = joystickContainer.value
  const centerX = container.offsetWidth / 2
  const centerY = container.offsetHeight / 2
  
  let x = pos.x - centerX
  let y = pos.y - centerY
  
  // Calculate distance from center
  const distance = Math.sqrt(x * x + y * y)
  
  // Limit distance to maxJoystickDistance
  if (distance > maxJoystickDistance) {
    const angle = Math.atan2(y, x)
    x = Math.cos(angle) * maxJoystickDistance
    y = Math.sin(angle) * maxJoystickDistance
  }
  
  joystickPos.value = { x, y }
  
  // Emit movement values normalized to -1 to 1
  emit('move', {
    x: x / maxJoystickDistance,
    y: -y / maxJoystickDistance
  })
}

// Action handlers
const handleJump = (event) => {
  event.preventDefault()
  emit('jump')
}

const handleAttack = (event) => {
  event.preventDefault()
  emit('attack')
}

// Define emits
const emit = defineEmits(['move', 'jump', 'attack'])
</script>

<style scoped>
.arena-controls {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 120px;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  z-index: 1000;
}

.movement-controls {
  flex: 1;
}

.joystick-container {
  width: 120px;
  height: 120px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  position: relative;
  cursor: pointer;
}

.joystick {
  width: 50px;
  height: 50px;
  background: #e38f00;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: transform 0.1s ease;
  pointer-events: none;
}

.action-controls {
  display: flex;
  gap: 20px;
}

.action-button {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: none;
  background: #e38f00;
  color: white;
  font-weight: bold;
  font-size: 14px;
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
}

.action-button.jump {
  background: #4a90e2;
}

.action-button.attack {
  background: #d74d24;
}

.action-button:active {
  transform: scale(0.95);
}
</style>
