<template>
  <div class="arena-page">
    <h1>BITCAT Arena</h1>
    <div class="game-container">
      <arena-game-field ref="gameField" />
    </div>
    <arena-controls 
      @move="handleMove"
      @jump="handleJump"
      @attack="handleAttack"
    />
  </div>
</template>

<script setup>
import { ref } from 'vue'
import ArenaGameField from '@/components/arena/ArenaGameField.vue'
import ArenaControls from '@/components/arena/ArenaControls.vue'

const gameField = ref(null)
const MOVE_SPEED = 5

const handleMove = ({ x, y }) => {
  if (!gameField.value) return
  
  const currentPos = gameField.value.playerPosition
  const newX = currentPos.x + (x * MOVE_SPEED)
  
  // Ensure player stays within game bounds
  const boundedX = Math.max(20, Math.min(newX, 580))
  
  gameField.value.updatePlayerPosition(boundedX, currentPos.y)
}

const handleJump = () => {
  if (!gameField.value) return
  gameField.value.jumpPlayer()
}

const handleAttack = () => {
  if (!gameField.value) return
  gameField.value.attackPlayer()
}
</script>

