<template>
  <div class="arena-game-field">
    <div class="game-status">
      <div class="player-info">
        <div class="hp-bar">
          <div :style="{ width: playerHP + '%' }" class="hp-fill"></div>
        </div>
        <span>You</span>
      </div>
      <div class="timer">{{ formatTime(gameTime) }}</div>
      <div class="player-info opponent">
        <div class="hp-bar">
          <div :style="{ width: botHP + '%' }" class="hp-fill"></div>
        </div>
        <span>Bot</span>
      </div>
    </div>
    
    <div class="game-field" ref="gameField">
      <div 
        class="player"
        :style="{ 
          left: playerPosition.x + 'px',
          bottom: playerPosition.y + 'px'
        }"
      >
        ðŸ˜º
      </div>
      <div 
        class="player bot"
        :style="{ 
          left: botPosition.x + 'px',
          bottom: botPosition.y + 'px'
        }"
      >
        ðŸ¤–
      </div>
      
      <div v-for="platform in platforms" 
           :key="platform.id" 
           class="platform"
           :style="{
             left: platform.x + 'px',
             bottom: platform.y + 'px',
             width: platform.width + 'px'
           }"
      ></div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted, readonly } from 'vue'

// Game state
const playerPosition = ref({ x: 50, y: 0 })
const botPosition = ref({ x: 300, y: 0 })
const playerHP = ref(100)
const botHP = ref(100)
const gameTime = ref(120) // 2 minutes
const platforms = ref([
  { id: 1, x: 0, y: 0, width: 600 }, // ground
  { id: 2, x: 100, y: 100, width: 200 },
  { id: 3, x: 400, y: 150, width: 150 },
])

// Physics state
const playerVelocity = ref({ x: 0, y: 0 })
const isJumping = ref(false)
const GRAVITY = 0.8
const JUMP_FORCE = 15
const ATTACK_RANGE = 50
const ATTACK_DAMAGE = 10
const MOVEMENT_SPEED = 5

// Game field reference
const gameField = ref(null)

// Format time function
const formatTime = (seconds) => {
  const mins = Math.floor(seconds / 60)
  const secs = seconds % 60
  return `${mins}:${secs.toString().padStart(2, '0')}`
}

// Physics update
const updatePhysics = () => {
  // Apply gravity
  playerVelocity.value.y -= GRAVITY
  
  // Update position
  playerPosition.value.y += playerVelocity.value.y
  
  // Check platform collisions
  for (const platform of platforms.value) {
    if (playerPosition.value.y <= platform.y &&
        playerPosition.value.y + playerVelocity.value.y > platform.y &&
        playerPosition.value.x >= platform.x &&
        playerPosition.value.x <= platform.x + platform.width) {
      playerPosition.value.y = platform.y
      playerVelocity.value.y = 0
      isJumping.value = false
      break
    }
  }
  
  // Prevent falling below ground
  if (playerPosition.value.y < 0) {
    playerPosition.value.y = 0
    playerVelocity.value.y = 0
    isJumping.value = false
  }
}

// Game loop with requestAnimationFrame for smoother updates
let animationFrame
const gameLoop = () => {
  if (gameTime.value > 0) {
    updatePhysics()
    updateBotPosition()
  }
  animationFrame = requestAnimationFrame(gameLoop)
}

// Timer update
let timerInterval
onMounted(() => {
  // Start animation loop
  gameLoop()
  
  // Update timer separately
  timerInterval = setInterval(() => {
    if (gameTime.value > 0) {
      gameTime.value--
    }
  }, 1000)
})

onUnmounted(() => {
  if (animationFrame) cancelAnimationFrame(animationFrame)
  if (timerInterval) clearInterval(timerInterval)
})

// Player actions
const jumpPlayer = () => {
  if (!isJumping.value) {
    playerVelocity.value.y = JUMP_FORCE
    isJumping.value = true
  }
}

const attackPlayer = () => {
  const distance = Math.abs(playerPosition.value.x - botPosition.value.x)
  if (distance <= ATTACK_RANGE) {
    botHP.value = Math.max(0, botHP.value - ATTACK_DAMAGE)
  }
}

// Simple bot AI
const updateBotPosition = () => {
  // Basic movement towards player
  const direction = botPosition.value.x < playerPosition.value.x ? 2 : -2
  botPosition.value.x += direction
  
  // Simple attack when close
  const distance = Math.abs(botPosition.value.x - playerPosition.value.x)
  if (distance <= ATTACK_RANGE) {
    playerHP.value = Math.max(0, playerHP.value - 1) // Bot deals 1 damage when close
  }
}

// Expose necessary methods and state for the parent component
defineExpose({
  playerPosition: readonly(playerPosition),
  updatePlayerPosition: (x) => {
    playerPosition.value.x = Math.max(20, Math.min(x, 580)) // Bound within game area
  },
  jumpPlayer,
  attackPlayer
})
</script>

